<template>
<main>
    <ImageModal v-if="modalVisible" @close="modalVisible = false" :imagedata="modalData"/>
    <Header :header="content"/>
    <input type="text" placeholder="نام قطعه مدنظر را جستجو نمایید ..." v-model="filter" style="width:500px;display:block;margin:10px auto;font-family:yekan;"/>
    <div class="container list">
        <table class="table table-hover container" >
            <thead>
                <tr>
                <th scope="col">ردیف</th>
                <th scope="col">نام قطعه</th>
                <th scope="col">نام ست</th>
                <th scope="col">تاریخ انقضا</th>
                <th scope="col">حداکثر دفعات استفاده</th>
                <th scope="col">وضعیت ایستگاهی</th>
                <th scope="col">تصویر</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(part,index) in filteredRows" :key="part">
                <th scope="row">{{index+1}}</th>
                <td>{{part.partname}}</td>
                <td>{{part.setname}}</td>
                <td>{{part.date}}</td>
                <td>{{part.usenum}}</td>
                <td>{{part.workstation}}</td>
                <td><img  src="../../assets/gallery.png" style="widtg:24px;height:24px;cursor:pointer;"  @click="openModal(part)">
                </td>
                </tr>
            </tbody>
        </table>
    </div>
 
</main>
</template>

<script>
import ImageModal from '../../components/ImageModal.vue'
import Header from '../../components/Header.vue'

export default {
    name:'PartsReport',
    components:{Header,ImageModal},
    data() {
        return {
            modalVisible: false,
            modalData: null,
            content:'لیست قطعات در حال استفاده',
            filter:'',
            usingparts:[
                {
                partname:'قیچی',
                setname:'ست اورژانس',
                date:'2022/2/12',
                usenum:12,
                workstation:'خروج از CSSD',
                pic:'back'
                },
                {
                partname:'تیغ',
                setname:'ست اتاق عمل',
                date:'2029/8/22',
                usenum:102,
                workstation:'بسته بندی',
                pic:'or'
                },
                {
                partname:'پد',
                setname:'ست اتاق عمل',
                date:'2022/12/15',
                usenum:0,
                workstation:'خروج ست کثیف',
                pic:'defect'
                },
                
            ] 
        }
    },
    computed: {
         filteredRows() {
            return this.usingparts.filter(part => {
            const setname = part.setname.toString().toLowerCase();
            const partname = part.partname.toLowerCase();
            const searchTerm = this.filter.toLowerCase();

            return partname.includes(searchTerm) ||
            setname.includes(searchTerm);
            });
         }
    },
    methods: {
        
        openModal(data) {
            this.modalData = data;
            this.modalVisible = true;
        }
    
    },
    
      
      
   
    
}
</script>

<style lang="scss" scoped>
.list{
        max-height: 65vh;
        overflow-y: scroll;
        table{
            
            direction: rtl;
            background-color: rgb(255, 255, 255);
            border-radius: 2px;
            text-align: center;
    
                thead{
                    background-color: #082C68;
                    color: rgb(255, 255, 255);
                    
        
                }
                    
        }
    }
</style>